.Profile{

    margin-top: 60px;

    .container{
        display: flex;
        gap: 20px;

    }
    .left-part{ //posts
        flex: 2;
        overflow-y: auto;
    }
    .right-part{ // follower & to follow
        flex: 1;
        margin-top: 20px;


        .profile-card{
            border: 1px solid var(--border-color);
            border-radius: 4px;

            text-align: center;
            padding: 40px;


            .user-img{
                width: 80px; 
                height: 80px;
                border-radius: 50%;

            }
            .user-bio{
                margin-block: 1rem;
            }
            .user-name,.follower-info{

                margin-top: 12px;

            }
            .follower-info{
                display: flex;
                justify-content: center;
                align-items: center;
                gap: 40px;
            }
            
            .follow,.update-profile{
                margin-top: 20px;
                display:  inline-block;
                
            }


        }
    }
}